import { createSlice } from "@reduxjs/toolkit";

const ui = createSlice({
  name: "ui",
  initialState: {
    activeGraphId: 1,
  },
  reducers: {
    setActiveGraph: (state, action) => {
      const { graphId } = action.payload;
      state.activeGraphId = graphId;
    },
  },
});

export const actions = ui.actions;

export default ui.reducer;
