import React, { useEffect, useState } from "react";
import {
  AppBar,
  Grid,
  Tab,
  Tabs,
  Toolbar,
  Typography,
} from "@material-ui/core";

import "./App.css";
import { useDispatch, useSelector } from "react-redux";
import Notifier from "./components/Notifier";
import V1Graph from "./components/V1Graph";
import V2Graph from "./components/V2Graph";
import V3Graph from "./components/V3Graph";
import { actions as uiActions } from "./store/slices/ui";
import { actions as v1GraphActions } from "./store/slices/v1Graph";
import { actions as v2GraphActions } from "./store/slices/v2Graph";
import { actions as v3GraphActions } from "./store/slices/v3Graph";

const App = () => {
  const activeGraphId = useSelector((state) => state.ui.activeGraphId);
  const dispatch = useDispatch();

  const handleTabClick = (event, graphId) => {
    dispatch(uiActions.setActiveGraph({ graphId }));
  };

  useEffect(() => {
    dispatch(v1GraphActions.reset());
    dispatch(v2GraphActions.reset());
    dispatch(v3GraphActions.reset());
  }, [activeGraphId]);

  return (
    <div className="root">
      <Notifier />
      <AppBar className="appbar" position="relative">
        <Toolbar>
          <Grid container justify="space-between" alignItems="center">
            <Grid item>
              <Typography variant="h6">
                Shortest Path using Dynamic Programming
              </Typography>
            </Grid>
            <Grid item>
              <Tabs value={activeGraphId} onChange={handleTabClick}>
                <Tab label="v1" value={1} />
                <Tab label="v2" value={2} />
                <Tab label="v3" value={3} />
              </Tabs>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      {activeGraphId === 1 && <V1Graph />}
      {activeGraphId === 2 && <V2Graph />}
      {activeGraphId === 3 && <V3Graph />}
    </div>
  );
};

export default App;
